.nav{
    height: 71px;
    background-color: aliceblue;
    /* margin-bottom: 10px; */
    box-shadow: 0px 1px 8px #a0d9e8;
    display: flex;
    align-items: center;
    /* position: static; */
    position: fixed; /* Set the navbar to fixed position */
  top: 0; /* Position the navbar at the top of the page */
  width: 100%;
}
.icon{
   
    flex: 0.48;
}
.logo{
    margin-top: 10px;
    width: 20vw;
}
@media (max-width: 425px){
    .nav{
        width: 100%;
    }
    .logo{
        width: 50vw;
    }
}